@import "../../Variables.scss";

.ChatTextAreaContainer {
	position: relative;
	flex-grow: 1;

	padding-right: 20px;
	margin-top: 2.5px;

	.RichTextArea {
		position: relative;
		flex-grow: 1;

		.SelectedText {
			background-color: $primaryColor;
		}

		&.Focused {
			.Caret {
				animation-name: CaretBlink;
				animation-duration: 1s;
				animation-iteration-count: infinite;

				@keyframes CaretBlink {
					0% {
						opacity: 1;
					}

					25% {
						opacity: 1;
					}

					100% {
						opacity: 0;
					}
				}
			}
		}
	}

	.ChatTextArea {
		pointer-events: none;
		opacity: 0;
	}

	.Renderer {
		position: absolute;
		bottom: 0;
		left: -10px;
		right: 10px;
		min-height: 100%;
		max-height: 50vh;

		margin-left: 10px;

		overflow-x: hidden;
		overflow-y: auto;

		box-sizing: border-box;

		border: 2px solid transparent;
		outline: none;
		resize: none;

		padding: 7px 10px;

		background-color: var(--darken-bg);
		backdrop-filter: blur(20px);

		cursor: text;

		white-space: pre-wrap;

		.Caret {
			display: inline-block;

			width: 2px;
			height: 20px;

			margin-right: -2px;
			border-radius: 10px;

			background-color: $primaryColor;

			opacity: 0;

			transform: translate(1px, 4px);
		}

		&.Placeholder {
			opacity: 0.5;

			display: flex;
			align-items: center;

			> svg {
				width: 20px;
				height: 20px;
			}
		}
	}

	.Button {
		margin: 0 5px;
		margin-top: 7px;

		cursor: pointer;

		> svg {
			padding: 1px;
			box-sizing: border-box;
		}
	}
}
