@import "../../../Variables.scss";

.AvatarContainer {
	background-color: var(--darken-bg);

	> .Avatar {
		object-fit: cover;
		object-position: center;
	}

	> .DefaultAvatar {
		width: 30px;
		height: 30px;
	}
}

.UserMention {
	display: inline-flex;
	align-items: center;
	justify-content: center;

	font-weight: bolder;
	transform: translateY(2px);

	> .Username {
		margin-left: 5px;
	}
}
